.activity-container{
  width: 500px;
  min-height: 65px;
  background-color: #1b1b32;
  margin: 5px auto;
  padding: 8px 15px 8px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 8px;
  border: 1px solid white;
  cursor: pointer;
}

.activity-text {
  width: 100%;
  height: 100%;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-wrap: anywhere;
}

.activity-icon{
  height: 25px;
  width: 25px;
  margin: 5px;
}

.activity-container.completed{
  background-color: #5a01a7;
  text-decoration: line-through;
}